import Constants from '../constant';

export const color = (state = {}, action) => {
	switch(action.type) {
		case Constants.ADD_COLOR:
			return {
				id: action.id,
                title: action.title,
                color: action.color,
                timestamp: action.timestamp,
                rating: 0
			}
		case Constants.RATE_COLOR:
            return (state.id !== action.id) ?
                state :
                {
                    ...state,
                    rating: action.rating
                }
        default :
            return state
	}
}

export const colors = (state = [], action) => {
    switch (action.type) {
        case Constants.ADD_COLOR :
            return [
                ...state,
                color({}, action)
            ]
        case Constants.RATE_COLOR :
            return state.map(
                c => color(c, action)
            )
        case Constants.REMOVE_COLOR :
            return state.filter(
                c => c.id !== action.id
            )
        default:
            return state
    }
}

export const sort = (state = "SORT_BY_DATE", action) => {
    switch (action.type) {
        case Constants.SORT_COLOR:
            return action.sortBy
        default :
            return state
    }
}


